<template>
    <div ref="document">
        <section id="project-accept-page">
            <spinner :data="spinnerData"></spinner>
            <div class="container">
                <response-message :data="errorData"></response-message>
                <div v-if="getDocument.company" class="project-accept-area">
                    <div class="row">
                        <div class="col-sm-12 col-md-8 col-lg-8">
                            <div class="bidding-title">
                                <h4>Licence for {{ getDocument.document ? getDocument.document.document_name : ''}}</h4>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-4 col-lg-4" style="justify-content: flex-end;align-items: center;display: flex">
                            <div class="get-button bidding" style="background-color: transparent">
                                <button style="margin-right: 10px" class="get-btn" @click="downloadPDF()">Download</button>
                                <button style="margin-right: 10px" class="get-btn">Email</button>
                                <button style="margin-right: 10px" @click="printDocument()" class="get-btn">Print</button>
                            </div>
                        </div>
                    </div>
                    <div class="row" id="row">
                        <div class="col-sm-12 col-md-8 col-lg-12">
                            <div class="printBody" id="printBody">
                                <div class="printDocument" id="printDocument">
                                    <div class="so">
                                        <div class="header">
                                            <img :src=" getDocument.company ? getDocument.company.company_logo : ''">
                                        </div>
                                        <h1 class="main-title">{{ getDocument.document ? getDocument.document.document_name : ''}}</h1>
                                        <p class="sub-main-title">{{ getDocument.document ? getDocument.document.document_sub_name : '' }}</p>
                                        <p class="sub-main-title"> E-PIN : <code>{{ getDocument.pin ? getDocument.pin.pin_text : '' }}</code> </p>
                                        <div class="certificate-info">
                                            <div class="grid-2">
                                                <p class="cert-p">Certificate No:  <strong>{{ getDocument.pin ? getDocument.pin.certificate : '' }}</strong></p>
                                                <ol v-for="(document,index) in getDocument.field" :key="index">
                                                    <li>{{ document.field_requirement_name }}</li>
                                                </ol>
                                                <p class="cert-p">Vehicle Name:  <strong>{{ getDocument.vehicle ? getDocument.vehicle.vehicle_name : '' }}</strong></p>
                                            </div>
                                            <div class="grid-2">
                                                <p class="cert-p">Policy No:  <strong>{{ getDocument.pin ? getDocument.pin.policy : '' }}</strong></p>
                                                <ol class="no-style" v-for="(document,index) in getDocument.field" :key="index">
                                                    <li>{{ document.field_value }}</li>
                                                </ol>
                                                <p class="cert-p">Vehicle Make:  <strong>{{ getDocument.vehicle ? getDocument.vehicle.vehicle_make : '' }}</strong></p>
                                            </div>

                                        </div>
                                        <div class="break-point">
                                            <p>{{ getDocument.document ? getDocument.document.document_footer : '' }}</p>
                                        </div>
                                        <div class="footer-certificate">
                                            <div class="grid-2 baseCenter">
                                                <div class="apply-signature">
                                                    <p>Examined</p>
                                                    <div class="apply__"></div>
                                                </div>
                                            </div>
                                            <div class="grid-2 ">
                                                <div class="main-signature">
                                                    <p class="holder">
                                                        {{ getDocument.company ? getDocument.company.company_name : '' }}
                                                        <br>
                                                        <small>Authorised Insurers</small>
                                                    </p>
                                                    <div class="main-sig-img ">
                                                        <img :src=" getDocument.company ? getDocument.company.company_signature : ''">
                                                    </div>
                                                    <p class="position">
                                                        Managing Director
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
                <div v-if="externalDocument.policy_number" class="project-accept-area">
                    <div class="row">
                        <div class="col-sm-12 col-md-8 col-lg-8">
                            <div class="bidding-title">
                                <h4>Licence for {{ externalDocument.document ? externalDocument.document.document_name : ''}}</h4>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-4 col-lg-4" style="justify-content: flex-end;align-items: center;display: flex">
                            <div class="get-button bidding" style="background-color: transparent">
                                <button style="margin-right: 10px" class="get-btn" @click="downloadPDF()">Download</button>
                                <button style="margin-right: 10px" class="get-btn">Email</button>
                                <button style="margin-right: 10px" @click="printDocument()" class="get-btn">Print</button>
                            </div>
                        </div>
                    </div>
                    <div class="row" id="row">
                        <div class="col-sm-12 col-md-8 col-lg-12">
                            <div class="printBody" id="printBody">
                                <div class="printDocument" id="printDocument">
                                    <div class="so">
                                        <div class="header">
                                            <img :src=" externalDocument.company ? externalDocument.company.company_logo : ''">
                                        </div>
                                        <h1 class="main-title">{{ externalDocument.document ? externalDocument.document.document_name : ''}}</h1>
                                        <p class="sub-main-title">{{ externalDocument.document ? externalDocument.document.document_sub_name : '' }}</p>
                                        <p class="cert-p">Policy No:  <strong>{{ externalDocument.policy_number ? externalDocument.policy_number : '' }}</strong></p>
                                        <div class="certificate-info">
                                            <div class="grid-2">
                                                <ol v-for="(document,index) in externalDocument.field" :key="index">
                                                    <li>{{ document.field_requirement_name }}</li>
                                                </ol>
                                            </div>
                                            <div class="grid-2">
                                                <ol class="no-style" v-for="(document,index) in externalDocument.field" :key="index">
                                                    <li>{{ document.field_value }}</li>
                                                </ol>
                                                <p class="cert-p">Registration Number:  <strong>{{ externalDocument.Registration_No ? externalDocument.Registration_No : '' }}</strong></p>
                                                <p class="cert-p">Expiration Date:  <strong>{{ externalDocument.Expiration_Date ? externalDocument.Expiration_Date : '' }}</strong></p>
                                                <p class="cert-p">Cover Date:  <strong>{{ externalDocument.Cover_Date ? externalDocument.Cover_Date : '' }}</strong></p>

                                            </div>

                                        </div>
                                        <div class="break-point">
                                            <p> Third Party URL :: {{ externalDocument.url ? externalDocument.url : '' }}</p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>

            </div>
        </section>
        <section id="followers">
            <div class="container">
            </div>
        </section>
    </div>
</template>

<script>
    // import 'paper-css/paper.min.css'
    import html2canvas from 'html2canvas';
    import jsPDF from 'jspdf';
    import Printd from 'printd'
    import download from 'downloadjs';
    import htmlToImage from 'html-to-image';
    import ls from '../../functions/localstorage';
    export default {
        name: "licenceDocument",
        data() {
            return {
                getDocument: {},
                docName: "",
                errorData: {},
                externalDocument: {},
                spinnerData: {},
            }
        },
        mounted() {
            this.generateDocument();
            // $("body").addClass("A5");
            document.body.scrollTop = document.documentElement.scrollTop = 0;
        },
        methods: {
            showLoading(options = {}) {
                let loader = this.spinnerData = {
                    spinner: 'Google',
                    isLoading: options.show !== undefined ? options.show : true ,
                    color: '#00bfff',
                    size: '60px',
                    options: 'center absolute',
                };
                return loader;
            },
            downloadPDF() {
                // here we convert the print document html to jpg image
                this.showLoading();
                htmlToImage.toJpeg(document.getElementById('printDocument'), { quality: 0.95 })
                    .then(function (dataUrl) {
                        // after conversion, we create a pdf document with it
                        var imgData = dataUrl;
                        var doc = new jsPDF();
                        doc.addImage(imgData, 'JPEG', 25, 0, 180, 280)
                        let certName = ls.getStorage("documentName");
                        doc.save(certName + '.pdf');
                    });
                this.showLoading({show:false});
            },
            printDocument() {
                console.log('printing')
                const d = new Printd()
                const cssText = `
                    @page { size: A5 }
                    body {
                        background-color: #fff !important;
                    }
                    .printDocument {
                        position: relative;
                        width: 21cm;
                        height: 29.7cm;
                        padding: 20px 45px;
                        /*border: double 4px #eee;*/
                        box-sizing: border-box;
                        /* background-color: rgba(229, 229, 229, 0.32); */
                        box-shadow: 0 5px 20px rgba(0,0,0,.2);
                         background-color: #fff;
                        /* height: 100%; */
                        /*background-size: 50px 50px;*/
                        display: block;
                    }
                    .printDocument .header {
                        width: 100%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        height: 200px;
                    }
                    .printDocument .header img {
                        max-width: 177px;
                    }
                    .main-title {
                        margin: 10px 0 0 0;
                        text-align: center;
                        color: red;
                        font-size: 30px;
                    }
                    .sub-main-title {
                        color: #888;
                        margin:0;
                        padding:0;
                        text-align:center;
                        text-transform: uppercase;
                        font-size: 13px;
                    }
                    .certificate-info {
                        width: 100%;
                        padding: 10px;
                        box-sizing: border-box;
                    }
                    .grid-2 {
                        width: 50%;
                        float:left;
                        box-sizing: border-box;
                    }
                    .cert-p {
                        font-size: 16px;
                        margin:0;
                    }
                    .certificate-info ol {
                        padding: 0 14px;
                        margin: 0;
                    }
                    .certificate-info ol.no-style {
                        padding: 0 14px;
                        margin: 0;
                        list-style:none;
                    }
                    .no-style li {
                        font-weight: bold;
                    }
                    .certificate-info li {
                        font-size: 15px;
                    }
                    .break-point {
                        width: 100%;
                        float: left;
                        margin-top: 70px;
                    }
                    .footer-certificate {
                        float: left;
                        width: 100%;
                        padding: 10px;
                        position: absolute;
                        bottom: 0;
                        box-sizing: border-box;
                        left: 0;
                    }
                    .footer-certificate .apply-signature {
                        float: left;
                        width: 100%;
                    }
                    .baseCenter {
                        height: 120px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                    }
                    .apply-signature p {
                        font-size: 14px;
                        margin:0;
                        font-weight: bold;
                        float:left;
                    }
                    .apply-signature .apply__ {
                        border-bottom: dotted 2px #4f4f4f;
                        width: 183px;
                        height: 2px;
                        float: left;
                        position: relative;
                        top: 10px;
                        left: 2px;
                    }
                    .main-signature {
                        float:right;
                        width: 100%;
                        margin-bottom: 10px;
                    }
                    .main-signature .holder {
                        margin:0;
                        font-size: 14px;
                        font-weight:bold;
                        width: 70%;
                        margin: 0 auto;
                        padding:0;
                        line-height: 1.1em;
                        text-align: center;
                    }
                    .main-signature .position {
                        margin:0;
                        font-size: 14px;
                        padding:0;
                        line-height: 1.1em;
                        text-align: center;
                    }
                    .main-sig-img {
                        width: 100%;
                        float: left;
                        height: 79px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }
                    .main-sig-img img {
                        width: 101px;
                    }


                    @page { margin: 0 }
                    body { margin: 0 }
                    .sheet {
                        margin: 0;
                        overflow: hidden;
                        position: relative;
                        box-sizing: border-box;
                        page-break-after: always;
                    }

                    /** Paper sizes **/
                    .A3               .sheet { width: 297mm; height: 419mm }
                    .A3.landscape     .sheet { width: 420mm; height: 296mm }
                    .A4               .sheet { width: 210mm; height: 296mm }
                    .A4.landscape     .sheet { width: 297mm; height: 209mm }
                    .A5               .sheet { width: 148mm; height: 209mm }
                    .A5.landscape     .sheet { width: 210mm; height: 147mm }
                    .letter           .sheet { width: 216mm; height: 279mm }
                    .letter.landscape .sheet { width: 280mm; height: 215mm }
                    .legal            .sheet { width: 216mm; height: 356mm }
                    .legal.landscape  .sheet { width: 357mm; height: 215mm }

                    /** Padding area **/
                    .sheet.padding-10mm { padding: 10mm }
                    .sheet.padding-15mm { padding: 15mm }
                    .sheet.padding-20mm { padding: 20mm }
                    .sheet.padding-25mm { padding: 25mm }

                    /** For screen preview **/
                    @media screen {
                        .sheet {
                            background: white;
                            box-shadow: 0 .5mm 2mm rgba(0,0,0,.3);
                            margin: 5mm auto;
                        }
                    }

                    /** Fix for Chrome issue #273306 **/
                    @media print {
                        .A3.landscape { width: 420mm }
                        .A3, .A4.landscape { width: 297mm }
                        .A4, .A5.landscape { width: 210mm }
                        .A5                    { width: 148mm }
                        .letter, .legal    { width: 216mm }
                        .letter.landscape      { width: 280mm }
                        .legal.landscape       { width: 357mm }

                    }
                    .hidden-img {

                    }
                    .printBody {
                        width: 21cm;
                        height: 29.7cm;
                        background-color: blue;
                        margin: 20px auto;
                    }
                `;
                const printCallback = ({ launchPrint }) => launchPrint()
                d.print(document.getElementById('printDocument'), [cssText], '', printCallback)
            },
            generateDocument() {
                this.showLoading();
                // $(".so").hide();
                if (this.$ls.checkStorage("localCartItem")) {
                    var lCart = this.$ls.getStorage("localCartItem");
                    var tData = this.$ls.getStorage("transaction_data")[0];
                    lCart = lCart[0];

                    const fullData = {
                        company_name: lCart.company_name,
                        product_id: lCart.product_id,
                        company_id: lCart.company_id,
                        data: lCart.data,
                        user_id: lCart.user_id,
                        vehicle_id: lCart.vehicle_id,
                        totalAmount: lCart.totalAmount,
                        reference: tData.txRef,
                    }

                    this.axios.post('/products/checkout', fullData)
                        .then(response => {
                            this.showLoading({show:false});
                            // $(".so").show();
                            // this.$store.commit("GET_DOCUMENT_FORM", response.data.data.document);

                            if (response.status === 200) {
                                // console.log(response.data.data.status)
                                const responseData = response.data.data;
                                if (response.data.data.status === false) {
                                    this.errorData = {
                                        isError: true,
                                        message: `Error :: ${responseData.message}, do review your info`,
                                    }
                                }else {
                                    if (responseData.external_url === true) {
                                        const document = {
                                            policy_number: responseData.Policy_Number,
                                            url: responseData.url,
                                            registration_no: responseData.Registration_No,
                                            cover_date: responseData.Cover_Date,
                                            expiration_date: responseData.Expiration_Date,
                                            company: responseData.company,
                                            product: responseData.product,
                                            category: responseData.category,
                                            document: responseData.document,
                                            field: responseData.field,
                                            reference: responseData.reference,
                                        }
                                        this.externalDocument = document
                                    }else {
                                        this.getDocument = response.data.data.document;
                                    }

                                    if (this.$ls.clearStorage("documentName")) {
                                        this.$ls.setStorage("documentName",response.data.data.document.document.document_name.replace(/\s/g, "_"));
                                        this.$ls.clearStorage("localCartItem")
                                        this.$ls.clearStorage("transaction_data")
                                    }

                                }
                            }
                        })
                        .catch(error => {
                            this.showLoading({show:false});
                            this.errorData = {
                                isError: true,
                                message: 'Sorry an error occurred return home',
                            }
                        })
                }else {
                    // console.log("no item")
                    this.$router.push("/");
                }
            },
        },
    }
</script>

